<script>
// Import Swiper Vue.js components
import { swiper, swiperSlide, Virtual } from "vue-awesome-swiper";
import {clientHeight} from "@/api/clientHeight.js"

import detailLayoutVue from "@/layout/otherLayout/detailLayout.vue";
export default {
  components: {
    detailLayoutVue,
    swiper,
    swiperSlide,
  },
  data() {
    return {
      Virtual,
      swiperOption: {
        observeParents: true,
        slidesPerView: 1, //每次滑动图片一张
        slidesPerGroup: 1, //每个div只有一张图片
        loop: true, //false 不循环    true循环
        // grabCursor: true,  //鼠标光标
        autoplay: {
          delay: 2000, //秒
          stopOnLastSlide: false,
          disableOnInteraction: false, //滑动不会失效
        },
        direction: "vertical",
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        },
      },
      isShowTitleFocus: false,
      isShowTitleFilter:false
    };
  },
  mounted() {
    this.$bus.$on("scrollTopEvent", this.titleScrollMove);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.titleScrollMove);
  },
  methods:{
    titleScrollMove(){
      let titleTop = this.$refs.moveTitleRef.getBoundingClientRect().top
      let filterTop = this.$refs.moveFilterRef.getBoundingClientRect().top

      let glap = titleTop - clientHeight;
      let glap2 = filterTop - clientHeight;


      if(!this.isShowTitleFocus && glap<0){
        this.isShowTitleFocus = true
      }

      if( glap2<0 && !this.isShowTitleFilter){
        this.isShowTitleFilter = true
      }
    }
  }
};
</script>

